import React, { useEffect, useState }  from 'react';
import ReactPaginate from 'react-paginate';
import axios from 'axios';
import 'bootstrap-icons/font/bootstrap-icons.css';
import '../../css/admin/BookList.css';

function BookList(){
    const [keyword, setKeyword] = useState(''); //
    const [searching, setSearching] = useState(false); //
    const [bookList, setBookList] = useState([]); //

    //pagination
    const [pageCount, setPageCount] = useState(0); // 
    const [currentPage, setCurrentPage] = useState(0); // 

    const bookPerPage = 10; //  



    useEffect(() => {
        scrollTop();
        if(searching){
            searchKeyword();
        }else{
            getBooks();
        }
    },[currentPage]);

    const scrollTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    //
    const getBooks = async() => {
        try{
            const resp = await axios.get('/api/adminBook/getAllBook',{
                params: {
                    page : currentPage+1,
                    pageSize : bookPerPage
                }
            });
            setBookList(resp.data.books);
            setPageCount(Math.ceil(resp.data.totalCount/bookPerPage));
        }catch(error){
            console.log('   error, ', error);
        }
    }

    const handlePageClick = (e) => {
        setCurrentPage(e.selected);
    }

    //
    const searchKeyword = async(e) => {
        console.log(": ", keyword);

        if(!keyword){
            alert(' .');
            return;
        }
        setSearching(true);

        try{
            const resp = await axios.get('/api/adminBook/searchResult',{
                params: {
                    page : currentPage+1,
                    pageSize : bookPerPage,
                    keyword
                }
            });

            setBookList(resp.data.books);
            setPageCount(Math.ceil(resp.data.totalCount/bookPerPage));
        }catch (error){
            console.log("   error ", error);
        }
    }

    //
    const deleteBook = async (bookNoToDelete) => {
        if(!window.confirm('?')){
            return;
        }
        try {
            //    bookNo 
            const resp = await axios.delete(`/api/adminBook/deleteBook?bookNo=${bookNoToDelete}`);

            alert(resp.data || " .");
            setBookList((prev)=> prev.filter((book)=>book.bookNo !== bookNoToDelete)); //  
        } catch (error) {
            console.error("   :", error);
            alert("   .");
        }
    };

    return(
        <div className="registed-book-list-container">
            <div className="book-search">
                <p><i className="bi bi-search"></i></p>
                <div className="search-box">
                    <input type="text"
                        value={keyword}
                        onChange={(e)=>setKeyword(e.target.value.trim())}
                        onKeyDown={(e) => e.key === 'Enter' && searchKeyword(e) && setCurrentPage(0)}
                        placeholder=", , , "
                    />
                </div>
            </div>
            <div className="book-regist">
                <div className="list-cnt">
                    <p> {bookList?.length || 0}</p>
                </div>
                <button type="button"></button>
                <div className="regist-way-container">
                    <div className="regist-way-box">
                        <div className="write-way">
                            <a href="./RegistWriteBook"></a>
                        </div>
                        <div className="search-way">
                            <a href="./RegistSearchBook"></a>
                        </div>
                    </div>
                </div>
            </div>

            <div className="registed-book-list">

                {bookList.length > 0 ? (
                    bookList.map((list, index)=>(
                        <div className="book-box" key={index}>
                            <div className="book-title">
                                <div className="book-no">{currentPage*bookPerPage + index + 1} | </div>
                                <p>{list.title}</p>
                            </div>
                            <div className="info">
                                <div className="book-img">
                                    <img src={list.image} alt=""/>
                                </div>
                                <div className="book-info">

                                    <div className="info-title">
                                        <p> </p>
                                        <p> </p>
                                        <p> </p>
                                        <p> </p>
                                    </div>
                                    <div className="info-cont">
                                        <p>{list.author}</p>
                                        <p>{list.genreName}</p>
                                        <p>{list.pubdate}</p>
                                        <p>{list.publisher}</p>
                                    </div>
                                </div>
                                <div className="book-review-cnt">
                                    <p>(231)</p>
                                </div>
                                <div className="info-btn">
                                    <button className="book-detail"></button>
                                    <button className="book-modify"></button>
                                    <button className="book-del" onClick={() => deleteBook(list.bookNo)}></button>
                                </div>
                            </div>
                        </div>
                    ))
                ):(
                    <div className="book-box">
                        <p style={{fontSize:"small"}}>   .</p>
                    </div>
                )}
            </div>
            {/* React Paginate  */}
            <ReactPaginate
                previousLabel={""} //   
                nextLabel={""} //   
                breakLabel={"..."} //   
                pageCount={pageCount} //   
                marginPagesDisplayed={1} //     
                pageRangeDisplayed={5} //      
                onPageChange={handlePageClick} //   
                containerClassName={"pagination"} //   
                activeClassName={"active"} //   
                forcePage={pageCount > 0 ? currentPage : undefined} //    
            />
        </div>
    );
}

export default BookList;